describe('Test angleExtent,startAngle, center content, innerBound and OuterBound property of Meter Circle ', () => {
  if (typeof jest === 'undefined') {
    it('Test for Right Half semi Meter Circle ', async function () {
      const content = render(<MeterCircle value={12} angleExtent={180} />);
      const firstElement = content.container.firstElementChild?.firstElementChild
        ?.firstElementChild as HTMLElement;
      expect(
        window.getComputedStyle(firstElement).getPropertyValue('height'),
        'Height of semi circle'
      ).to.be.equal('196px');
      expect(
        window.getComputedStyle(firstElement).getPropertyValue('width'),
        'Width of semi circle'
      ).to.be.equal('98px');
    });

    it('Test for Top Half semi Meter Circle ', async function () {
      const content = render(<MeterCircle value={12} angleExtent={180} startAngle={180} />);
      const firstElement = content.container.firstElementChild?.firstElementChild as HTMLElement;
      expect(
        window.getComputedStyle(firstElement).getPropertyValue('height'),
        'Height of semi circle'
      ).to.be.equal('98px');
      expect(
        window.getComputedStyle(firstElement).getPropertyValue('width'),
        'Width of semi circle'
      ).to.be.equal('196px');
    });

    it('Test for Bottom Half semi Meter Circle ', async function () {
      const content = render(<MeterCircle value={12} angleExtent={180} startAngle={0} />);
      const firstElement = content.container.firstElementChild?.firstElementChild as HTMLElement;
      expect(
        window.getComputedStyle(firstElement).getPropertyValue('height'),
        'Height of semi circle'
      ).to.be.equal('98px');
      expect(
        window.getComputedStyle(firstElement).getPropertyValue('width'),
        'Width of semi circle'
      ).to.be.equal('196px');
    });

    it('Test for Left Half semi Meter Circle ', async function () {
      const content = render(<MeterCircle value={12} angleExtent={180} startAngle={270} />);
      const firstElement = content.container.firstElementChild?.firstElementChild as HTMLElement;
      expect(
        window.getComputedStyle(firstElement).getPropertyValue('height'),
        'Height of semi circle'
      ).to.be.equal('196px');
      expect(
        window.getComputedStyle(firstElement).getPropertyValue('width'),
        'Width of semi circle'
      ).to.be.equal('98px');
    });

    it('Test Center content', async function () {
      const content = render(
        <MeterCircle value={20}>
          {() => {
            return <div>Sales in USD</div>;
          }}
        </MeterCircle>
      );
      const centerText = await content.findByText('Sales in USD');
      expect(centerText).not.null;
    });

    it('Test inner and outer bounds', async function () {
      const content = render(
        <MeterCircle value={20}>
          {(context) => {
            return (
              <div
                id="centerContentDiv"
                data-iBx={context.innerBounds.x}
                data-iBy={context.innerBounds.y}
                data-oBx={context.outerBounds.x}
                data-oBy={context.outerBounds.y}>
                <div>Sales in USD</div>
              </div>
            );
          }}
        </MeterCircle>
      );
      const centerContent = content.container.firstElementChild?.firstElementChild?.childNodes[2]
        .firstChild as HTMLElement;
      expect(Number(centerContent.dataset['ibx'])).closeTo(37, 1);
      expect(Number(centerContent.dataset['iby'])).closeTo(37, 1);
      expect(centerContent.dataset['obx']).equals('12');
      expect(centerContent.dataset['oby']).equals('12');
    });
  }
});

